/*
 * Hero Module
 * ======================================================================== */
.hero {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  background-color: #FFF;
  background-position: center center;
  background-size: cover;
  overflow: hidden;
  padding: 48px 28px;
  position: relative;
  -webkit-transform: translate3d(0, 0, 0);
  width: 100%;
  background-color: #000;
  color: #FFF; }
  @media (min-width: 1025px) {
    .hero {
      padding: 120px 88px; } }
  .hero__inner {
    margin: 0 0;
    max-width: 1120px;
    position: relative;
    width: 100%; }
    @media (min-width: 1025px) {
      .hero__inner {
        margin: 0 72px; } }
  .hero__content {
    max-width: 760px; }
    .hero__content *:last-child {
      margin-bottom: 0; }
  .hero__image {
    overflow: hidden;
    position: relative;
    background-position: center center;
    background-size: cover;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%; }
    .hero__image::after {
      background: linear-gradient(to right, black 0%, rgba(0, 0, 0, 0) 100%);
      content: '';
      height: 100%;
      left: 0;
      position: absolute;
      top: 0;
      width: 100%; }
  .hero__img {
    height: 100%;
    left: 0;
    object-fit: cover;
    position: absolute !important;
    top: 0;
    width: 100%; }
  .hero__eyebrow {
    font-family: "Sohne", sans-serif;
    font-weight: normal;
    text-transform: none;
    font-size: 14px;
    line-height: 1.71;
    color: #ED2A37;
    margin-bottom: 24px; }
  .hero__body p {
    font-family: "Sohne", sans-serif;
    font-weight: normal;
    text-transform: none;
    font-size: 14px;
    line-height: 1.71; }
    @media (min-width: 1025px) {
      .hero__body p {
        font-size: 16px;
        line-height: 1.5; } }

