/*
 * Rajdhani
 * ======================================================================== */

@font-face {
  font-family: 'Rajdhani';
  font-style: normal;
  font-weight: normal;
  src:
    url('../fonts/Rejdhani-Regular.woff') format('woff'),
    url('../fonts/Rejdhani-Regular.woff2') format('woff2');
}

/*
 * Sohne
